/*======================================================================
      Global
  ======================================================================*/

* {
  padding: 0;
  margin: 0;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  /* For some Androids */
  outline: none;
}

html {
  height: 100%;
  font-family: "microsoft yahei", Arial, "Helvetica Neue", Helvetica, sans-serif;
}

body {
  height: 100%;
  margin: 0  auto;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

p,
a,
button,
span {
  -webkit-font-smoothing: antialiased;
}

input {
  outline: none;
}

